type Story = StoryObj<TFilterGroupTemplateArgs>;

export default {
  title: 'Components/Filter (BETA)/Group/API',
  tags: ['autodocs', '!dev'],
  component: 'market-filter-group',
  argTypes: {
    hasSearch: {
      control: { type: 'boolean' },
      description: 'Whether to show search',
      name: 'Show search',
      table: { category: 'Demo' },
    },
  },
  args: {
    hasSearch: true,
  },
  parameters: {
    docs: {
      story: {
        autoplay: true,
      },
    },
  },
} as Meta<TFilterGroupTemplateArgs>;

export const API: Story = {
  render: (args) => DefaultTemplate(args),
  play: async ({ canvasElement }) => {
    await waitForMarketHydration(canvasElement);
    fixFilterZIndex(canvasElement);
  },
};

export const Small: Story = {
  ...API,
  args: {
    size: 'small',
  },
};

export const MoreVisibleFilters: Story = {
  ...API,
  args: {
    maxVisibleFilters: 9000,
  },
};
